.header {
  width: 100%;
  height: 48px;
  display: flex;
  align-items: center;
  padding: 0 16px;
  background: linear-gradient(180deg, #111822, #0b1118);
  color: #fff;
  box-sizing: border-box;
}

/* LEFT */
.left {
  display: flex;
  align-items: center;
  gap: 10px;
}

.iconBtn {
  background: none;
  border: none;
  color: #9aa4b2;
  font-size: 20px;
  cursor: pointer;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo {
  display: flex;
  align-items: center;
}

/* NAV CENTER */
.nav {
  display: flex;
  align-items: center;
  gap: 18px;
  margin: 0 auto;
}

.navItem {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 14px;
  color: #e5e7eb;
  text-decoration: none;
  opacity: 0.9;
}

.navItem:hover {
  opacity: 1;
}

/* RIGHT */
.right {
  display: flex;
  align-items: center;
  gap: 12px;
}

.loginBtn {
  border: 1px solid #3b82f6;
  color: #fff;
  padding: 6px 14px;
  border-radius: 8px;
  text-decoration: none;
  font-size: 14px;
}

.loginBtn:hover {
  background: #3b82f6;
}
